.banner {
    position: relative;
    height:500px;
    color: white;
    object-fit: contain;

}

.banner_contents{
    margin-left: 80px;
    padding-top: 300 px;
    height: 190px;
}
.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-top: 1rem;
    padding-bottom: 0.1rem;
}

.banner_description{
    
   
    
    width: 45rem;
    line-height: 1.3;
    
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;

}

.banner--fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180 deg,
        transparent,
        rgba(37,37,37,0.61),
        #111
    );
        
    }

.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.2rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;

}
.banner_button:hover{
    color:#000;
    background: color #e6e6e6;
    transition: all 0.2s;

}