.signupScreen{
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0,0,0,0.85);

}
.signupScreen > form {
    display: grid;
    flex-direction: column;

}
.signupScreen > form  > input{
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 50px;
    border: none;
    padding: 5px 15px;

}
.signupScreen > form  > button{
    padding: 16 20px;
    font-size: 1rem;
    color: white;
    border-radius: 5px;
    background-color: red;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
    height: 30px;

}

.signupScreen_link:hover{
    cursor: pointer;
    text-decoration: underline;
}