.loginScreen{
    position : relative;
    height: 100%;
    background: url("http://assets.nflxext.com/ffe/siteui/vlv3/757ab38f-5d08-40bc-b3eb-eaba63ed8203/93c34f94-56c8-40a7-8b2e-b4aac6427977/GB-en-20210125-popsignuptwoweeks-perspective_alpha_website_medium.jpg"
    )center no-repeat;
    background-size: cover;
}
.loginScreen_logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}
.loginScreen_button {
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: red;
    cursor: pointer;
    font-weight: 600;
    border: none;
}
.loginScreen_gradient{
    width:100%;
    z-index:1;
    height: 100vh;
    background: rgba(0,0,0,0.4);
    background-image: linear-gradient(to top,
    rgba(0,0,0,0.8) 0,
    rgba(0,0,0,0) 50%,
    rgba(0,0,0,0.8) 100%);
}
.loginScreen_body{
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
   z-index: 1;
   color: white;
   padding: 20px; 
   left:0;
   right:0;
}
.loginScreen_body > h1 {
    font-size: 3.125rem;
    margin-bottom: 20px;
}
.loginScreen_body > h2 {
    font-size: 2.125rem;
    font-weight: 400;
    
}
.loginScreen_input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;


}
.loginScreen_getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    background-color: red;
    border: none;
    font-weight: 600;
    cursor: pointer;
}
