.nav {
    position: fixed;
    top: 0;
    padding: 10px;
    width: 100%;
    height: 20px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
    background-color: #111;
    
}

.nav_contents {
    display: flex;
    justify-content: space-between;
    background-color: #111;
   

}
.net_black {
    
    background-color: #111;
}
.net_logo {
    position: fixed;
    top: 10px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;

}
.net_avatar {
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;

}
